body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
  "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
  sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #162126;
  color: #cfcdcd;
  letter-spacing: 0.05em;
}

ul {
  list-style: none;
}
.header {
  position: fixed;
  left: 0;
  right: 0;
  min-height: 53px;
  padding: 15px 0;
  background-color: #232f34;
  box-shadow: 1px -3px 3px 3px rgba(0, 0, 0, 0.54); }

.header + div {
  padding-top: 83px; }

.nav-list {
  display: flex;
  list-style: none; }

.nav-list-item {
  margin: 0 15px; }

.nav-list-item:hover a {
  color: #f9aa33; }

.nav-list-item a {
  font-size: 1.1rem;
  color: #fff;
  border-bottom: 2px solid transparent;
  text-decoration: none;
  -webkit-transition: color .2s ease-in-out;
  transition: color .2s ease-in-out; }

.nav-list-item a.is-active {
  color: #f9aa33;
  border-color: #f9aa33; }

.home-container {
  height: calc(100vh - 83px);
  background: url(/ricknmorty/static/media/rickandmorty2.825488f7.png) no-repeat center;
  background-size: cover; }

.home-title {
  width: 50%;
  margin-top: 15%;
  padding-left: 5%; }

.home-title h1 {
  color: #fff; }

.filter-form {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  margin: 20px 0; }

.filter-search {
  width: 250px;
  padding: 10px;
  margin: 10px 8px;
  font-weight: 700;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  color: #232f34; }

.filter-select {
  padding: 10px 10px 10px 25px;
  margin: 10px 8px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  color: #232f34; }

.filter-submit {
  width: 90px;
  min-height: 40px;
  font-weight: 700;
  margin: 10px 8px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 15px;
  background: #f9aa33;
  color: #232f34;
  cursor: pointer; }

.card {
  margin: 10px;
  padding-bottom: 15px;
  width: 250px;
  color: #7e85a0;
  background-color: #2a3f47;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 5px;
  box-shadow: 0px 0px 3px rgba(0, 0, 0, 0.12); }

.card-image {
  display: inline-block;
  width: 100%;
  border-radius: 4px 4px 0 0; }

.card-name {
  margin: 10px 0 0 15px;
  font-size: 1.3rem; }

.card-species, .card-gender {
  margin: 0 0 0 15px;
  color: #cfcbcb;
  font-weight: 300; }

.pagination {
  display: flex;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  justify-content: center;
  margin: 0 auto 20px;
  padding: 0;
  list-style: none; }

.pagination-item {
  padding: 5px 10px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-right: none;
  color: #232f34;
  background: #59676e;
  cursor: pointer; }

.pagination-item.active {
  background: #71828a;
  color: #fff; }

.pagination-item:last-child {
  border-right: 1px solid rgba(0, 0, 0, 0.12); }

.preloader {
  margin-top: 5%;
  text-align: center; }

.characters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 15px;
  overflow: auto; }

